@import url(https://fonts.googleapis.com/css2?family=Major+Mono+Display&display=swap);
.Profile img {
  width: 100%;
  max-width: 250px;
  border-radius: 50%;
  box-shadow: var(--prim-box-shadow);
}
.Profile {
  text-align: center;
}
.Profile h2 {
  text-decoration: underline;
}
.Profile svg {
  font-size: 30px;
  margin: 5px 10px 5px 0;
}

.Projects h2 {
  text-decoration: underline;
}
.Projects img {
  padding: 20px 0;
}

@-webkit-keyframes antiRotate {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
  }
}

@keyframes antiRotate {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
  }
}

.WheelItem {
  position: absolute;
  height: 70px;
  width: 70px;
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
  border-radius: 50%;
}
.WheelItem img {
  -webkit-animation: antiRotate 35s linear infinite;
          animation: antiRotate 35s linear infinite;
  height: 100%;
  width: 100%;
  object-fit: contain;
}

@-webkit-keyframes infiniteRotate {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes infiniteRotate {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.wheel-wrapper {
  position: relative;
  padding: 95px;
  border-radius: 50%;
  background-color: var(--prim-light);
}
.Wheel {
  position: relative;
  border-radius: 50%;
  -webkit-animation: infiniteRotate 35s linear infinite;
          animation: infiniteRotate 35s linear infinite;
}
.wheel-wrapper .text {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-family: "Major Mono Display", monospace;
  font-size: 22px;
  text-align: center;
  color: rgb(92, 75, 44);
}

.App {
  min-height: 100vh;
  padding: 2rem 0;
  overflow: hidden;
}

:root {
  --prim-dark: #d86e74;
  --prim-light: rgb(255, 242, 218);
  --prim-green: #96d86e;
  --prim-blue: #6e98d8;
  --prim-box-shadow: 0 3px 6px 0px rgb(0 0 0 / 50%);
}

body {
  margin: 0;
  background-color: rgb(43, 38, 41);
  font-family: "Major Mono Display", monospace;
  color: rgb(255, 242, 218);
  color: var(--prim-light);
}
p {
  margin: 0;
}
a {
  color: inherit;
}
a:hover {
  color: rgb(248, 208, 128);
}

